body {
  font-size: 16px;
  line-height: 18px;
  background-image: url("../img/kp_bg.jpg");
  background-size: 100%;
}
body .content .firstBlock,
body .content .secondBlock,
body .content .thirdBlock,
body .content .sixthBlock {
  height: 1000px;
}
body .content .fourthBlock {
  height: 1100px;
}
body .content .fifthBlock {
  height: 740px;
}
body .content .sixthBlock {
  height: 1450px;
}
body .content .seventhBlock {
  height: 1090px;
}
body .content .firstBlock .firstBlock__contacts {
  margin: 175px 0 0 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
body .content .firstBlock .firstBlock__contacts li p,
body .content .firstBlock .firstBlock__contacts li a {
  text-transform: uppercase;
}
body .content .firstBlock .firstBlock__contacts li:nth-child(1) p {
  font: 2.813em/1em "HeliosCondC";
  color: #fff;
}
body .content .firstBlock .firstBlock__contacts li:nth-child(2) a {
  font: 3em/1.5em "HeliosCond-Bold";
  color: #fff;
  text-decoration: none;
}
body .content .firstBlock .firstBlock__contacts li:nth-child(3) p {
  font: 1.563em/0.5em "HeliosCond-Bold";
  color: #fff;
}
body .content .firstBlock__logo img {
  margin: 135px 0 0 50px;
}
body .content .firstBlock__info {
  margin: 175px 0 0;
}
body .content .firstBlock__info li {
  list-style: disc;
  color: #bf172a;
}
body .content .firstBlock__info li p {
  font: 1.313em/2em "HeliosCond-Bold";
  color: #bf172a;
}
body .content button {
  margin: 20px 0 0;
  width: 425px;
  height: 90px;
  font: 4em/90px "AdigianaUltra";
  color: #dc0038;
  text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
  background-color: #2c9195;
  border: none;
  border-radius: 50px;
  outline: none;
  box-shadow: 7px 0 30px #fff, 0 7px 30px #fff, -7px 0 30px #fff, 0 -7px 30px #fff;
}
body .content .secondBlock .secondBlock__headline {
  margin: 135px 0 0;
  display: flex;
  justify-content: center;
  font: 3.5em/1em "AdigianaUltra";
  color: #fff;
  text-align: center;
  transform: rotate(-4deg);
}
body .content .secondBlock .secondBlock__video {
  margin: 110px 0 0 60px;
  width: 800px;
  height: 550px;
  background-color: #11808a;
  border-radius: 50px;
  box-shadow: 7px 0 10px #fff, 0 7px 10px #fff, -7px 0 10px #fff, 0 -7px 10px #fff;
  overflow: hidden;
}
body .content .secondBlock .secondBlock__video iframe {
  width: 100%;
  height: 100%;
}
body .content .thirdBlock .thirdBlock__headline {
  margin: 250px 0 0;
  display: flex;
}
body .content .thirdBlock .thirdBlock__headline p {
  font: 5em/1em "AdigianaUltra";
  color: #15828c;
  text-align: center;
  text-shadow: 7px 0 20px #fff, 0 7px 20px #fff, -7px 0 20px #fff, 0 -7px 20px #fff;
  transform: rotate(-4deg);
}
body .content .thirdBlock .thirdBlock__info {
  margin: 0 0 0 25px;
}
body .content .thirdBlock .thirdBlock__info li {
  margin: 50px 0;
  list-style: disc;
  color: #bf172a;
}
body .content .thirdBlock .thirdBlock__info li p {
  font: 1.875em/2em "HeliosCond-Bold";
  color: #bf172a;
}
body .content .fourthBlock .fourthBlock__headline {
  margin: 140px 0 0;
}
body .content .fourthBlock .fourthBlock__headline p {
  font: 3.875em/4em "LuckiestGuy-Regular";
  color: #fff;
  text-transform: uppercase;
}
body .content .fourthBlock .category h6 {
  font: 3.125em/90px "LuckiestGuy-Regular";
  color: #fed900;
  text-transform: uppercase;
  text-shadow: 5px 5px #00858e;
}
body .content .fourthBlock .category ul {
  list-style: disc;
  color: #fff;
}
body .content .fourthBlock .category ul li p {
  font: 1.875em/1.5em "HeliosCond-Bold";
  color: #fff;
}
body .content .fourthBlock .debutant h6 {
  margin: 40px 0 0 70px;
}
body .content .fourthBlock .debutant ul {
  margin: 40px 0 0 15px;
}
body .content .fourthBlock .nominee h6 {
  margin: 40px 0 0 170px;
}
body .content .fourthBlock .nominee ul {
  margin: 40px 0 0 117px;
}
body .content .fourthBlock .superstar h6 {
  margin: 40px 0 0 100px;
}
body .content .fourthBlock .superstar ul {
  margin: 40px 0 0 55px;
}
body .content .fourthBlock button {
  margin: -40px 0 0;
  width: 425px;
  height: 90px;
  font: 4em/90px "AdigianaUltra";
  color: #dc0038;
  text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
  background-color: #2c9195;
  border: none;
  border-radius: 50px;
  outline: none;
  box-shadow: 7px 0 30px #fff, 0 7px 30px #fff, -7px 0 30px #fff, 0 -7px 30px #fff;
}
body .content .fifthBlock .fifthBlock__headline h6 {
  margin: 150px 0 0;
  font: 3.75em/1em "AdigianaUltra";
  color: #15828c;
  text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
  transform: rotate(-4deg);
}
body .content .fifthBlock .fifthBlock__headline span {
  margin: 225px 0 0;
  display: block;
  font: 2.5em/1em "HeliosCond-Bold";
  color: #fff;
}
body .content .fifthBlock .fifthBlock_foto ul {
  margin: 155px 0 0;
  display: flex;
  justify-content: space-around;
}
body .content .fifthBlock .fifthBlock_foto ul li {
  width: 100%;
  display: flex;
  justify-content: center;
}
body .content .fifthBlock .fifthBlock_foto ul li img {
  width: 80%;
  border: 5px solid #fff;
  border-radius: 25px;
}
body .content .sixthBlock .sixthBlock__headline ul {
  margin: 0 0 0 50px;
}
body .content .sixthBlock .sixthBlock__headline ul li p {
  transform: rotate(-4deg);
}
body .content .sixthBlock .sixthBlock__headline ul li:nth-child(1) p {
  font: 12em/1em "AdigianaUltra";
  color: #f10f0f;
  text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
}
body .content .sixthBlock .sixthBlock__headline ul li:nth-child(2) {
  text-align: right;
}
body .content .sixthBlock .sixthBlock__headline ul li:nth-child(2) p {
  font: 7em/1em "AdigianaUltra";
  color: #f10f0f;
  text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
}
body .content .sixthBlock .sixthBlock__info h6 {
  margin: 120px 0 0;
  font: 7.5em/1em "AdigianaUltra";
  color: #15828c;
  text-shadow: 10px 0 20px #fff, 0 10px 20px #fff, -10px 0 20px #fff, 0 -10px 20px #fff;
  transform: rotate(-4deg);
}
body .content .sixthBlock .sixthBlock__info ul {
  margin: 110px 0 0;
}
body .content .sixthBlock .sixthBlock__info ul li {
  margin: 40px 0;
}
body .content .sixthBlock .sixthBlock__info ul li p {
  width: 93%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  font: 2.313em/1em "HeliosCond-Bold";
  color: #c1171e;
}
body .content .sixthBlock .sixthBlock__info ul li:nth-child(1) span {
  font-size: 2em;
}
body .content .sixthBlock .sixthBlock__info ul li:nth-child(2) span {
  font-size: 2em;
  color: #15828c;
  text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
}
body .content .sixthBlock .sixthBlock__info .adult {
  margin: 220px 0 0;
  font: 1.875em/1em "HeliosCond-Bold";
  color: #15828c;
  text-shadow: 5px 0 10px #fff, 0 5px 10px #fff, -5px 0 10px #fff, 0 -5px 10px #fff;
}
